import React from "react";
import { TaskState } from "../../../Store/tasks";
import { Dialog } from "../../Utility/Dialog/Dialog";
import { TaskStateChoice } from "./TaskStateChoice";

export type TaskStateEditionDialogProps = {
  /** The task to be rendered. */
  state: TaskState;
  /** Triggered when the task is being clicked. */
  onClose: (state?: TaskState) => void;
};

/** Edition of a task's state, displays all possible states. */
export const TaskStateEditionDialog: React.FunctionComponent<TaskStateEditionDialogProps> = ({
  state,
  onClose,
}) => {
  return (
    <Dialog
      buttons={[
        {
          text: "Annuler",
          onclick: () => {
            onClose();
          },
        },
      ]}
    >
      <TaskStateChoice state={state} onChange={onClose} />
    </Dialog>
  );
};
