import { AuthState } from "../Auth/AuthState";
import { authManager } from "../Auth/AuthManager";
import { AppAction, AppActionBase } from "./store";
import { useSelector } from "react-redux";
import { AppState } from "./appState";

export interface SetAuthStateAction extends AppActionBase {
  type: "SetAuthState";
  authState: AuthState;
}

export const createSetAuthStateAction = (
  authState: AuthState
): SetAuthStateAction => {
  return { type: "SetAuthState", authState };
};

export function authReducer(
  authState: AuthState = authManager.getState(),
  action: AppAction
): AuthState {
  switch (action.type) {
    case "SetAuthState":
      return { ...authState };
    default:
      return authState;
  }
}

/** Subsribe to the authentication manager's state, and store it in the app state. */
export const useAuth = (): AuthState =>
  useSelector<AppState, AuthState>((appState) => appState.authState);
