const Digits = "0123456789abcdef";

function getUniqueDigits(length: number) {
  var result = "";
  for (var i = 0; i < length; i++) {
    result += Digits.charAt(Math.floor(Math.random() * Digits.length));
  }
  return result;
}

export function newGuid() {
  return `${getUniqueDigits(8)}-${getUniqueDigits(4)}-${getUniqueDigits(
    4
  )}-${getUniqueDigits(4)}-${getUniqueDigits(12)}`;
}
