import React, { useEffect } from "react";
import styles from "./App.module.scss";
import { Switch, Route, useLocation, Redirect } from "react-router-dom";
import { useAuth } from "../../Store/auth";
import { ErrorBoundary } from "../ErrorBoundary/ErrorBoundary";
import { TaskList } from "../Tasks/TaskList";
import { useDataStorage, fetchData } from "../../Store/dataStorage";
import { MessageDialog } from "../Utility/MessageDialog/MessageDialog";
import { PageLayout } from "./PageLayout";
import { IconDesigner } from "../IconDesigner/IconDesigner";
import { useThunkDispatch } from "../../Store/store";
import { LoadStatus } from "../LoadStatus/LoadStatus";

function NoMatch() {
  let location = useLocation();

  return <>Invalid path:  {location.pathname} </>;
}

export const AppComponent = () => {
  const authState = useAuth();
  const dataStorage = useDataStorage();
  const thunkDispatch = useThunkDispatch();

  useEffect(() => {
    if (authState.status === "Authenticated") {
      fetchData(thunkDispatch);
    }
  }, [authState, thunkDispatch]);

  if (authState.status === "Error") {
    return (
      <MessageDialog>
        Authentication failed: "{authState.errorMessage}"
      </MessageDialog>
    );
  } else if (authState.status === "AuthenticationNeeded") {
    return (
      <LoadStatus done={0} total={2} message="Authentification ncessaire..." />
    );
  } else if (authState.status === "Authenticated") {
    if (!dataStorage.dataAvailable) {
      return (
        <LoadStatus
          done={1}
          total={2}
          message="Authentifi. En attente des donnes..."
        />
      );
    }
  }

  return (
    <div className={styles.app}>
      <ErrorBoundary>
        <Switch>
          <Route path="/" exact>
            <Redirect to="/tasks" />
          </Route>
          <Route path="/home" exact>
            <PageLayout>
              Home page
              <IconDesigner />
            </PageLayout>
          </Route>
          <Route path="/tasks" exact>
            <TaskList />
          </Route>
          <Route path="*">
            <NoMatch />
          </Route>
        </Switch>
      </ErrorBoundary>

      {dataStorage.status === "fetching" && (
        <MessageDialog>Chargement en cours...</MessageDialog>
      )}
      {dataStorage.status === "saving" && (
        <MessageDialog>Enregistrement en cours...</MessageDialog>
      )}
    </div>
  );
};
