import { TaskState } from "../Store/tasks";

export const getStateLabel = (state: TaskState): string => {
  switch (state) {
    case "created":
      return "En attente";
    case "in-backlog":
      return "Dans le radar";
    case "active":
      return "En cours";
    case "finished":
      return "Termine";
    case "abandoned":
      return "Abandonne";
  }
};
