import { AuthState } from "./AuthState";
import { OAuth2Client } from "google-auth-library";

export type AuthStateListener = (state: AuthState) => void;

export interface IAuthManager {
  /** Forcefully trigger a new authentication. */
  authenticate: () => void;

  /** Retrieve the OAuth2Client with enables accessing google services. */
  getOAuth2Client: () => OAuth2Client;

  /** Get the current autentication state. */
  getState: () => AuthState;

  /** Subscribe to be notified of state changes. Returns a function to unsubscribe. */
  subscribe: (listener: AuthStateListener) => () => void;
}

const scope = [
  "https://www.googleapis.com/auth/userinfo.profile",
  "https://www.googleapis.com/auth/drive",
];

// this class authenticates with Google. It stores the current authentication state (status, token)
// in the local storage, so a new connection is done only if the credentials are not readily
// available in the local storage.
// its getState and subscribe method can be used to inject the AuthState as React context.

class AuthManager implements IAuthManager {
  private readonly oAuth2Client = new OAuth2Client({
    clientId:
      "186913784324-548k2j5jsqlht13qhu2miegr42fu0v56.apps.googleusercontent.com",
    redirectUri: `${window.location.origin}`,
  });

  private state: AuthState;
  private listeners: AuthStateListener[] = [];

  public constructor() {
    this.oAuth2Client.transporter.configure();
    console.error("CONSTRUCT");

    const urlSearchParams = new URLSearchParams(window.location.hash.substr(1));
    var accessToken = urlSearchParams.get("access_token");

    if (accessToken) {
      /* We got an access token back from Google */

      const originalPage = urlSearchParams.get("state");
      if (!originalPage) {
        throw new Error("missing 'state' field.");
      }

      const expiresIn = urlSearchParams.get("expires_in");
      if (!expiresIn) {
        throw new Error("missing 'expires_in' field.");
      }

      const expiration = new Date();
      expiration.setSeconds(expiration.getSeconds() + parseInt(expiresIn));

      console.debug("Auth: TokenReceived");

      this.oAuth2Client.setCredentials({ access_token: accessToken });
      localStorage.setItem(
        "access_token",
        `${accessToken}|${expiration.toISOString()}`
      );

      window.history.replaceState(null, "", originalPage);
      this.state = { status: "Authenticated", expiration };
    } else {
      const accessTokenInfo = localStorage.getItem("access_token");
      var expiration: Date | null = null;
      if (accessTokenInfo) {
        /* The user is already authenticated. */

        const accessTokenInfoParts = accessTokenInfo.split("|");
        accessToken = accessTokenInfoParts[0];
        expiration = new Date(accessTokenInfoParts[1]);

        // reauthenticate if there is less than 10 minutes remaining
        if (expiration.getTime() < new Date().getTime() + 10 * 60 * 1000) {
          expiration = null;
        }
      }

      if (expiration) {
        this.oAuth2Client.setCredentials({ access_token: accessToken });
        this.state = { status: "Authenticated", expiration };
      } else {
        /* The user is not yet authenticated or its authentication will expire in less than 10 minutes. Redirect the user to Google authentication. */

        this.state = { status: "AuthenticationNeeded" };
        this.authenticate();
      }
    }
  }

  private setState = (state: AuthState) => {
    this.state = state;
    this.listeners.forEach((listener) => listener(state));
  };

  public subscribe = (listener: AuthStateListener): (() => void) => {
    this.listeners.push(listener);

    return () => {
      this.unsubscribe(listener);
    };
  };

  private unsubscribe = (listener: AuthStateListener) => {
    const index = this.listeners.indexOf(listener);
    if (index > -1) {
      this.listeners.splice(index, 1);
    }
  };

  public getState = (): AuthState => this.state;

  public getOAuth2Client = (): OAuth2Client => {
    return this.oAuth2Client;
  };

  public authenticate = () => {
    console.debug("Auth: RedirectingToGoogle");
    this.state = { status: "AuthenticationNeeded" };

    (async () => {
      // redirect the user to Google authentication

      const authorizeUrl = this.oAuth2Client.generateAuthUrl({
        scope,
        login_hint: "lucmoco@gmail.com",
        state:
          window.location.pathname +
          window.location.search +
          window.location.hash,
        response_type: "token",
      });

      window.location.href = authorizeUrl;
    })();
  };
}

declare global {
  interface Window {
    boussoleAuthManager?: AuthManager;
  }
}

if (!window.boussoleAuthManager) {
  window.boussoleAuthManager = new AuthManager();
}

// authenticate at startup and expose a singleton service.
export const authManager: IAuthManager = window.boussoleAuthManager;
