import React from "react";
import styles from "../Tasks.module.scss";
import { getClasses } from "../../../Helpers/cssHelper";
import { TaskState } from "../../../Store/tasks";
import { getStateLabel } from "../../../Helpers/textHerlper";

export type TaskStateDisplayProps = {
  /** State to be rendered. */
  state: TaskState;
  /** Show the text. */
  showText?: boolean;
  /** Display a thick border. */
  showCurrentBorder?: boolean;
  /** Display an attenuated color. */
  showNotCurrent?: boolean;
  /** Display as a button and call onClick when clicked. If undefined, the control is not clickable. */
  onClick?: () => void;
};

/** Shows a task state as a rectangle, with a background color corresponding to the state, and optionally the state's text label. */
export const TaskStateIndicator: React.FC<TaskStateDisplayProps> = ({
  state,
  showText,
  showCurrentBorder,
  showNotCurrent,
  onClick,
}) => {
  return (
    <div
      title={showText ? undefined : getStateLabel(state)}
      className={getClasses(
        styles.taskStateIndicator,
        styles[state],
        !showText && styles.small,
        showCurrentBorder && styles.thickBorder,
        showNotCurrent && styles.notCurrent,
        onClick && styles.clickable
      )}
      onClick={
        onClick
          ? (event) => {
              event.stopPropagation();
              onClick();
            }
          : undefined
      }
    >
      {showText && getStateLabel(state)}
    </div>
  );
};
