import React from "react";
import { Link } from "react-router-dom";
import { useAuth } from "../../Store/auth";
import { AuthStatus } from "../AuthStatus/AuthStatus";
import { Button } from "../Utility/Button/Button";
import { useFetchData, useSaveData } from "../../Store/dataStorage";
import reload from "../../Assets/reload.png";
import { SaveImage } from "../../Assets/SaveImage";

type PageLayoutProps = {
  /** optional header controls to be rendered */
  headerControls?: React.ReactElement;
};

/** renders a header with various buttons and the children as the content */
export const PageLayout: React.FunctionComponent<PageLayoutProps> = ({
  headerControls,
  children,
}) => {
  const authState = useAuth();
  const fetchData = useFetchData();
  const saveData = useSaveData();

  return (
    <>
      <header>
        <Link to="/home">Home</Link>
        <Link to="/tasks">Tches</Link>
        <AuthStatus authState={authState} />
        <Button
          onClick={fetchData}
          isLight={true}
          title="Recharger les donnes"
        >
          <img src={reload} alt="Recharger les donnes" />
        </Button>
        <Button onClick={saveData} isLight={true} title="Recharger les donnes">
          <SaveImage height={20} width={20} fill="white" />
        </Button>
        {headerControls}
      </header>
      <section>{children}</section>
    </>
  );
};
