import React, { useState, useMemo } from "react";
import { useTaskService, Task, getNewTask } from "../../Store/tasks";
import { Dialog } from "../Utility/Dialog/Dialog";
import { TaskForm } from "./TaskForm";

export type TaskEditionDialogProps = {
  task: Task | undefined;
  onClose: () => void;
};

export function TaskEditionDialog(props: TaskEditionDialogProps) {
  const id = useMemo(() => (props.task ? props.task.id : undefined), [
    props.task,
  ]);
  const [editedTask, setEditedTask] = useState<Task>(
    props.task || getNewTask()
  );
  const { createTask, updateTask } = useTaskService();

  const onSave = () => {
    if (id) {
      updateTask(id, editedTask);
    } else {
      createTask(editedTask);
    }
    props.onClose();
  };

  return (
    <Dialog
      title={id ? "Modifier une tche" : "Nouvelle tche"}
      buttons={[
        { text: "Valider", onclick: onSave },
        { text: "Annuler", onclick: props.onClose },
      ]}
    >
      <TaskForm task={editedTask} onChange={setEditedTask} />
    </Dialog>
  );
}
