import { AppState } from "./appState";
import { AppActionBase, AppAction, useAppDispatch } from "./store";
import { newGuid } from "../Helpers/guidHelper";
import { useSelector } from "react-redux";

// *** Model ***

export type TaskState =
  | "created"
  | "in-backlog"
  | "active"
  | "finished"
  | "abandoned";

export interface Task {
  id: string;
  state: TaskState;
  title: string;
  description: string; // may be empty, never null or undefined
  created: Date;
  modified: Date;
  dateClosed?: Date; // date the task was closed. undefined if "state" is not "closed".
}

export interface TaskModel {
  [id: string]: Task;
}

export const getNewTask = (): Task => ({
  id: "",
  state: "created",
  title: "",
  description: "",
  created: new Date(),
  modified: new Date(),
});

// *** Actions ***

interface UpdateTaskAction extends AppActionBase {
  type: "UpdateTask";
  id: string;
  task: Partial<Task>;
}

interface CreateTaskAction extends AppActionBase {
  type: "CreateTask";
  task: Task;
}

export type TaskActions = UpdateTaskAction | CreateTaskAction;

// *** Reducer ***

function updateTask(tasks: TaskModel, id: string, updates: Partial<Task>) {
  var task = tasks[id];

  if (!task) throw new Error(`No task found with id {id}`);

  tasks[id] = {
    ...task,
    ...updates,
  };

  return { ...tasks };
}

function createTask(tasks: TaskModel, task: Task) {
  const id = newGuid();
  const now = new Date();

  tasks[id] = {
    ...task,
    id,
    created: now,
    modified: now,
  };

  return { ...tasks };
}

export function taskReducer(
  tasks: TaskModel = {},
  action: AppAction
): TaskModel {
  switch (action.type) {
    case "UpdateTask":
      return updateTask(tasks, action.id, action.task);
    case "CreateTask":
      return createTask(tasks, action.task);
    default:
      return tasks;
  }
}

// *** Service ***

export const useTasks = () =>
  useSelector<AppState, TaskModel>((appState) => appState.tasks);

export interface TaskService {
  createTask: (task: Task) => void;
  updateTask: (id: string, task: Partial<Task>) => void;
}

export function useTaskService(): TaskService {
  const dispatch = useAppDispatch();

  return {
    createTask: (task) => dispatch({ type: "CreateTask", task }),
    updateTask: (id, task) => dispatch({ type: "UpdateTask", id, task }),
  };
}

export const newState = (state: TaskState): Partial<Task> => {
  return {
    state,
    dateClosed:
      state === "finished" || state === "abandoned" ? new Date() : undefined,
  };
};
