import React from "react";
import styles from "../Tasks.module.scss";
import spacingStyles from "../../Utility/Spacing.module.scss";
import { TaskState } from "../../../Store/tasks";
import { TaskStateIndicator } from "./TaskStateIndicator";

export type TaskStateChoiceProps = {
  state: TaskState;
  onChange: (state: TaskState) => void;
};

/** Shows all possible task states and enables selecting a state. */
export const TaskStateChoice: React.FC<TaskStateChoiceProps> = ({ state, onChange }) => {
  const renderState = React.useCallback(
    (buttonState: TaskState) => {
      return (
        <TaskStateIndicator
          state={buttonState}
          showText
          showCurrentBorder={buttonState === state}
          showNotCurrent={buttonState !== state}
          onClick={
            buttonState === state
              ? undefined
              : () => {
                  onChange(buttonState);
                }
          }
        />
      );
    },
    [state, onChange]
  );

  return (
    <div className={spacingStyles.spaceOutVerticalComponents}>
      {renderState("created")}
      {renderState("in-backlog")}
      {renderState("active")}
      <div className={styles.taskStateClosed}>
        {renderState("finished")}
        {renderState("abandoned")}
      </div>
    </div>
  );
};
