import React from "react";
import {
  createStore as reduxCreateStore,
  combineReducers,
  Reducer,
  applyMiddleware,
  Action,
  Dispatch,
} from "redux";
import thunk, { ThunkDispatch } from "redux-thunk";
import { TaskActions, taskReducer } from "./tasks";
import { AppState } from "./appState";
import { DataStorageActions, dataStorageReducer } from "./dataStorage";
import {
  authReducer,
  SetAuthStateAction,
  createSetAuthStateAction,
} from "./auth";
import { Provider, useDispatch } from "react-redux";
import { authManager } from "../Auth/AuthManager";

export interface AppActionBase extends Action<string> {
  type: string;
}

export type AppAction = TaskActions | DataStorageActions | SetAuthStateAction;

export type AppThunkDispatch = ThunkDispatch<AppState, never, AppAction>;

const combinedReducers: Reducer<AppState, AppAction> = combineReducers<
  AppState,
  AppAction
>({
  authState: authReducer,
  tasks: taskReducer,
  dataStorage: (dataStorageModel) =>
    dataStorageModel || { status: "inactive", dataAvailable: false },
});

const reducer: Reducer<AppState, AppAction> = (appState, action) => {
  appState = combinedReducers(appState, action);
  appState = dataStorageReducer(appState, action);
  return appState;
};

export const useAppDispatch: () => Dispatch<AppAction> = () =>
  useDispatch<AppAction>() as any;

export const useThunkDispatch: () => AppThunkDispatch = () =>
  useDispatch<AppThunkDispatch>();

export const StoreProvider: React.FunctionComponent = ({ children }) => {
  // create the store
  const store = React.useMemo(() => {
    return reduxCreateStore<AppState, AppAction, {}, {}>(
      reducer,
      applyMiddleware(thunk)
    );
  }, []);

  // Save the auth state in the store
  React.useMemo(() => {
    return authManager.subscribe((authState) =>
      store.dispatch(createSetAuthStateAction(authState))
    );
  }, [store]);

  return <Provider store={store}>{children}</Provider>;
};
